<!--
   button 

   @param {Boolean} isDisabled 
   @param {String} [type=default|primary|outline|error|link] 
   @param {String} [size=normal|large|small] 
   @param {String} appendClass class
   @param {Object} appendStyle Style
-->

<template>
  <button :style="appendStyle" :class="['vc-btn', typeClass, sizeClass, appendClass, {'vc-btn-disabled': isDisabled}]">
    <span>
        <slot></slot>
    </span>
  </button>
</template>

<script>
  import {componentBaseParamConfig} from '../base-config';

  export default {
      props: Object.assign({}, componentBaseParamConfig, {
          type: {
              type: String,
              default: 'default'
          },
          size: {
              type: String,
              default: 'normal'
          },
          isDisabled: {
              type: Boolean,
              default: false
          }
      }),

      computed: {
          typeClass() {
              return {
                  'default': '',
                  'primary': 'vc-btn-primary',
                  'outline': 'vc-btn-outline',
                  'error': 'vc-btn-error',
                  'link': 'vc-btn-link'
              }[this.type];
          },

          sizeClass() {
              return {
                  'normal': '',
                  'large': 'vc-btn-lg',
                  'small': 'vc-btn-sm'
              }[this.size];
          }
      }
  }
</script>

<style lang="sass">
  @import "style.scss";
</style>


