<!--
   table 

   @param {Array} data table tbody
   ex:
          data: [{
            key: 0,
            name: 'name0',
            age: 32,
            address: 'address0'
          }, {
            key: 1,
            name: 'name1',
            age: 33,
            address: 'address1'
          }]


   @param {Array} columns table thead,rander
   ex:
           columns: [{
                        title: '',
                        dataIndex: 'name',
                        render(text, item) {
                          return `<a href="#">${text}</a>`;
                        }
                      }, {
                        title: '',
                        dataIndex: 'age'
                      }, {
                        title: '',
                        dataIndex: 'address'
                      }, {
                        title: '',
                        render(text, item) {
                          return `<a class="vc-inline" @click="clickOperate"></a>
                                    <span class="vc-divider"></span>
                                  <a class="vc-inline"></a>`;
                        }
                    }]
   @param {Function} onChangePage 
   @param {Function} onSelectAll hasAllSelecttruecheckbox
   @param {Number} currentPage 
   @param {Number} pageSize 
   @param {Number} total 
   @param {Function} onSizeChange 
   @param {Boolean} isShowPagination 
   @param {Boolean} isReal ,,
   @param {Boolean} hasAllSelect 
   @param {Function} trClick ,
   @param {Function} onCheckboxClick 
   @param {String} emptyText ,html
   @param {Number} colsNum 
   @param {String} appendClass class
   @param {Object} appendClass Style
-->
<template>
    <div>
        <div :style="appendStyle" :class="['vc-table', appendClass]">
            <div class="cols vc-hidden" ref="hiddenCols" v-if="colsHidden">
                <slot></slot>
            </div>
            <div class="vc-table-body">
                <pv-table-header :header="header" :body="body" :on-select-all="onSelectAll" :has-select-all="hasSelectAll"></pv-table-header>
                <!--  -->
                <div v-show="!data || data.length === 0" class="vc-table-empty-text" v-html="emptyText">
                </div>
                <pv-table-body v-show="data && data.length > 0" :body="body" :data="data" :page-size="pageSizeShow" :has-select-all="hasSelectAll"
                               :current-page="currentPageShow"></pv-table-body>
            </div>
        </div>

        <pv-pagination :total="totalNum"
                       v-if="isShowPagination"
                       :current-page="currentPage"
                       :page-size="pageSize"
                       :on-size-change="onSizeChangeFn"
                       :on-page-change="onPageChangeFn"></pv-pagination>
    </div>
</template>

<script>
    import pvPagination from '../pagination'
    import pvCheckbox from '../checkbox'
    import pvTableHeader from './header'
    import pvTableBody from './body'
    import {componentBaseParamConfig} from '../base-config';

    export default {
        components: {
            pvPagination,
            pvTableHeader,
            pvTableBody
        },
        props: Object.assign({}, componentBaseParamConfig, {
            isShowPagination: {
                type: Boolean,
                default: true
            },
            onChangePage: {
                type: Function,
                default() {}
            },
            onSizeChange: {
                type: Function,
                default() {}
            },
            currentPage: {
                type: Number,
                default: 1
            },
            pageSize: {
                type: Number,
                default: 5
            },
            total: {
                type: Number
            },
            data: {
                type: Array,
                default() {
                    return []
                }
            },
            colsNum: Number,
            hasSelectAll: {
                type: Boolean,
                default: false
            },
            emptyText: {
                type: String,
                default: ','
            },
            onSelectAll: {
                type: Function,
                default: () => {
                    return;
                }
            }
        }),

        data() {
            return {
                header: [],
                body: [],
                totalNum: this.total || this.data.length,
                pageSizeShow: this.pageSize,
                currentPageShow: this.currentPage,
                colsHidden: true
            }
        },

        methods: {
            onSizeChangeFn(val) {
                this.pageSizeShow = val;
                this.onSizeChange && this.onSizeChange(val);
            },
            onPageChangeFn(val) {
                this.currentPageShow = val;
                this.onPageChange && this.onPageChange(val);
            }
        },

        mounted() {
            this.data.forEach((it, index) => {
                it.index = index;
            });

            this.colsHidden = false;
        }
    }
</script>

<style lang="sass">
    @import "style.scss";
</style>


